.log_out_container {
  position: absolute;
  padding: 20px 20px;
  background-color: #443a42;
  right: 0px;
  top: 0px;
  z-index: 6;
  -webkit-animation-name: log_out_container_show;
          animation-name: log_out_container_show;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  display: none;
}

.log_out_container div {
  width: 100px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: orange;
  color: #443a42;
  cursor: pointer;
}

.logout_check {
  margin-top: 20px;
}

.log_out_container_disappear {
  -webkit-animation-name: log_out_container_show_no_show;
          animation-name: log_out_container_show_no_show;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;

  -webkit-transform: translate(300px, 0);

          transform: translate(300px, 0);
}

@-webkit-keyframes log_out_container_show_no_show {
  from {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  to {
    -webkit-transform: translate(100%, 0px);
            transform: translate(100%, 0px);
  }
}

@keyframes log_out_container_show_no_show {
  from {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  to {
    -webkit-transform: translate(100%, 0px);
            transform: translate(100%, 0px);
  }
}
@-webkit-keyframes log_out_container_show {
  from {
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0);
  }
  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}
@keyframes log_out_container_show {
  from {
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0);
  }
  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

